






























































import Vue from 'vue';
import axios from 'axios';
import {Util} from '../shared/Util';
export default Vue.extend({
  created(){
    if (Util.isLogedIn()) {
      this.loggedIn = true;
      this.userEmail = localStorage.getItem(Util.USER_EMAIL);
    }
  },
  methods:{
    logout(){
      Util.logout()
      this.$router.push("login")
    },
  },
  data(){
    return ({
      loggedIn: false,
      userEmail: localStorage.getItem(Util.USER_EMAIL)
    })
  }
})



